import { Injectable } from '@angular/core';
import { IViewDesignerInspectorConfig } from '../public-interfaces/view-designer-inspector.types';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RxViewDesignerInspectorService {
  private onChangeSubject = new Subject<{ guid: string; config: IViewDesignerInspectorConfig }>();
  private configs: Map<string, IViewDesignerInspectorConfig> = new Map();

  onChange$ = this.onChangeSubject.asObservable();

  clear(): void {
    this.configs.clear();
  }

  set(guid: string, config: IViewDesignerInspectorConfig): void {
    this.configs.set(guid, config);
    this.onChangeSubject.next({ guid, config });
  }

  get(guid: string): IViewDesignerInspectorConfig {
    return this.configs.get(guid);
  }

  delete(guid: string): void {
    this.onChangeSubject.next({ guid, config: null });
    this.configs.delete(guid);
  }
}
